import React from 'react'
import Product from './Product'
import list from "./data";
import "./amazon.css";


const ProductPage = ({handleClick}) => {
  return (
    <section>
    {list.map((item) => (
      <Product key={item.id} item={item} handleClick={handleClick} />
    ))}
  </section>
  )
}

export default ProductPage